import { Link } from "gatsby"
import PropTypes from "prop-types"
import React from "react"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faCoffee } from '@fortawesome/free-solid-svg-icons'



const LeftMenu = () => (
	<nav className="navbar navbar-expand navbar-dark bg-dark flex-md-column flex-row align-items-start py-2">
	    <div className="collapse navbar-collapse align-items-start">
	        <ul className="flex-md-column flex-row navbar-nav w-100 justify-content-between">
	            <li className="nav-item">
	                <a className="nav-link pl-0 text-nowrap" href="#"><i className="fa fa-bullseye fa-fw"></i> <span className="font-weight-bold">CS101</span></a>
	            </li>
	            <li className="nav-item">
	                <Link className="nav-link pl-0" to="/ml101/"><FontAwesomeIcon icon="coffee" /> <span className="d-none d-md-inline">Home</span></Link>
	            </li>
	            <li className="nav-item">
	            	<Link className="nav-link pl-0" to="/ml101/rating/"><div className="azam-icon"></div> <span className="d-none d-md-inline">Rating</span></Link>
	            </li>
	            <li className="nav-item">
	            	<Link className="nav-link pl-0" to="/ml101/homeworks"><div className="azam-icon"></div> <span className="d-none d-md-inline">Homeworks</span></Link>
	            </li>
	            <li className="nav-item">
	            	<Link className="nav-link pl-0" to="/ml101/materials"><div className="azam-icon"></div> <span className="d-none d-md-inline">Materials</span></Link>
	            </li>
	            <li class="nav-item">
	            	<Link className="nav-link pl-0" to="/ml101/projects"><div className="azam-icon"></div> <span className="d-none d-md-inline">Projects</span></Link>
	            </li>
	        </ul>
	    </div>
	</nav>
)


export default LeftMenu


