import React from "react"
import PropTypes from "prop-types"

import { Link } from "gatsby"

import { Row, Col, Container } from "react-bootstrap" 
import LeftMenu from "./leftMenu"


const CourseLayout = ({ children }) => {
  return (


    <div className="container-fluid course-fluid">
		<Row>

		{/*    
		    <Col sm={2} className="leftBar">
		    	<LeftMenu />
		    </Col>
		    <Col sm={10} className="scrollCourse">
		    	<div className="course-starter">
					{children}
				</div>
		    </Col>
	 	 */}

			<div className="container-fluid h-100">
			    <div className="row h-100">
			        <aside className="col-12 col-md-2 p-0 bg-dark fixed-top leftSideBar">
			        	<LeftMenu />
			        </aside>

			        <main className="col offset-md-2 bg-faded py-3 centralContent">
			            {children}
			        </main>
			    </div>
			</div>

		</Row>
	</div>
  )
}

CourseLayout.propTypes = {
  children: PropTypes.node.isRequired,
}


export default CourseLayout

